<template>
  <div>
    <headerBar :header-data="headerData" @searchClick="searchAction()" />
    <div class="container">
      <common-list
        ref="dida_list"
        class="common-list"
        :post-url="list.postUrl"
        :req-data="list.reqData"
        :data-name="list.dataName"
        :apptype-show="false"
        @returnData="getPrizeLog($event)"
      >
        <div class="content">
          <div class="common-main box">
            <div class="common-list">
              <div
                v-for="(item, index) in list.items"
                :key="index"
                :class="{ focus: ind === index }"
                class="list"
                @click="onitemClick(index)"
              >
                <h3>{{ item.bt }}</h3>
                <div class="info">
                  <span v-if="item.sfzd == 1" class="top">
                    置顶
                  </span>
                  <span>
                    <i class="iconfont iconlianxiren" />
                    {{ item.fbrxm }}
                  </span>
                  <span>
                    <i class="iconfont iconshijian" />
                    {{ item.fbsj | formatTime14 }}
                  </span>
                </div>
              </div>
            </div>
          </div>
        </div>

      </common-list>
    </div>
    <!-- <FooterNav /> -->
    <searchMeun @goSearchAction="searchXmxx($event)" @reSet="reSet">
      <van-field v-model="search.xmmc" label="公告名称" placeholder="请输入公告名称" input-align="right" clearable />
      <van-cell title="选择开始日期" :value="date" is-link @click="show = true" />
      <van-cell title="选择截止日期" :value="date1" is-link @click="show1 = true" />
      <van-calendar v-model="show" :min-date="minDate" color="#1890ff" @confirm="onConfirm" />
      <van-calendar v-model="show1" :min-date="minDate" color="#1890ff" @confirm="onConfirm1" />
    </searchMeun>

  </div>

</template>

<script land="script" scoped>
// import FooterNav from '@/components/footerNav/footerNav.vue'
import constants from '../../../common/js/constants'
import commonList from '@/components/list/commonList1.vue'
import { pageRouter } from '@/common/js/mixin.js'
import headerBar from '@/components/header/headerBar.vue'
import searchMeun from '@/components/searchComponents/searchMeun.vue'

import { mapState, mapMutations } from 'vuex'
export default {
  name: 'XtggIndex',
  components: {
    // xyScroll,
    // FooterNav,
    commonList,
    headerBar,
    searchMeun
    // [ScrollView.name]: ScrollView,
    // [ScrollViewMore.name]: ScrollViewMore,
    // [ScrollViewRefresh.name]: ScrollViewRefresh
  },
  mixins: [pageRouter],
  data() {
    return {
      minDate: new Date(1999, 0, 1),
      date: '请选择日期',
      date1: '请选择日期',
      show: false,
      show1: false,
      ind: null, // 点击事件
      query: '',
      // dbsxList: [], // 数据展示列表
      pageno: 1, // 页
      totalpageno: 1, // 总页数
      nomore: false, // 没有更多数据
      isFinished: false,
      xtggPage: {
        pageStyles: 0, // 页面状态展示
        pageTitle: ''
      },
      isLoading: false,
      showSearch: false,
      prizeLog: [],
      list: {
        items: [],
        error: false, // 是否在刷新
        postUrl: '',
        reqData: '',
        dataName: '',
        noDataText: ''
      },
      headerData: {
        title: '系统公告',
        leftUrl: '/MhHome',
        leftText: ' ',
        showSearch: true
      },
      search: {
        xmmc: ''
      },
      SeachShow: false,
      ksrq: '',
      jzrq: ''

    }
  },
  computed: {
    ...mapState({
      userInfo: state => state.mh.mhUserInfo
    })
  },
  created() {
    this.XMZT = constants.XMZT
    this.CGFS = constants.CGFS
    this.TimeDay = constants.TimeDay
    this.getxtgglist()
  },
  mounted() {
    const reqData = {
    }
    this.$refs.dida_list.onRefresh(reqData)
  },
  methods: {
    formatDate(date) {
      return `${date.getFullYear()}/${date.getMonth() + 1}/${date.getDate()}`
    },
    formatDate1(date) {
      const year = date.getFullYear()
      let month = ''
      let day = ''
      if ((date.getMonth() + 1) < 10) {
        month = '0' + (date.getMonth() + 1)
      } else {
        month = date.getMonth() + 1
      }
      if (date.getDate() < 10) {
        day = '0' + (date.getDate())
      } else {
        day = date.getDate()
      }
      return year + '' + month + day
    },
    onConfirm(date) {
      this.ksrq = this.formatDate1(date)
      this.date = this.formatDate(date)
      // this.minDate1 = date
      this.show = false
    },
    onConfirm1(date) {
      this.jzrq = this.formatDate1(date)
      this.date1 = this.formatDate(date)
      // this.maxDate = date
      this.show1 = false
    },
    reSet() {
      this.search.xmmc = ''
      this.date = '请选择日期'
      this.date1 = '请选择日期'
      this.ksrq = ''
      this.jzrq = ''
    },
    ...mapMutations([
      'SET_DBSXLIST_ITEM'
    ]),
    searchAction() {
      this.SeachShow = true
    },
    getPrizeLog(data) {
      this.list.items = data
    },
    getxtgglist() {
      this.list.postUrl = 'erp/getXtggAction'
      this.list.reqData = {
        bt: this.search.xmmc,
        kssj: this.ksrq,
        jzsj: this.jzrq
      }
      this.list.dataName = 'xtggArr'
    },
    onitemClick(index) {
      this.$router.push({
        name: 'XtggGgxq',
        params: {
          id: this.list.items[index].id
          // bt: this.list.items[index].bt,
          // fbrxm: this.list.items[index].fbrxm,
          // fbsj: this.list.items[index].fbsj,
          // nr: this.list.items[index].fbsjnr
        }
      })
    },
    // 搜索点击事件
    searchXmxx(type) {
      this.$bus.$emit('showMeun', false)
      const reqData = {
        bt: this.search.xmmc,
        kssj: this.ksrq,
        jzsj: this.jzrq
      }
      this.$refs.dida_list.onRefresh(reqData)
    }
  }
}
</script>

